.Cart {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0;
  padding: 5px 15px;
  box-sizing: border-box;
  max-width: 1160px;
  background-color: #fff;
  border-bottom: 2px solid #ce3322;
}
.CartImgName {
  width: 320px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.CartImg img {
  max-width: 100px;
}

.CartName {
  max-width: 200px;
  font-size: 16px;
  line-height: 18px;
  font-weight: 700;
  color: rgb(91, 91, 200);
}
.CartPrice {
  color: #ce3322;
  font-weight: 700;
}
.CartButtonClose {
  text-align: right;
}
.CartButtonClose img {
  cursor: pointer;
  background-color: none;
  background: #ce3322;
}
.CartButtonClose img:hover {

  background: red;
}
.Blue {
  color: blue;
  font-weight: 700;
}
.Red {
  color: red;
  font-weight: 700;
}
.Black {
  color: black;
  font-weight: 700;
}